"use client";

import { useEffect, useState } from "react";

export default function Page() {
  const [pong, setPong] = useState<string>("");

  useEffect(() => {
    (async () => {
      // Next dev/SSR 안전 가드 (electron 없을 때)
      if (!window.electron) {
        setPong("not-electron");
        return;
      }

      // ping: { ok, ts }
      const res = await window.electron.ping();
      setPong(res.ok ? `pong @ ${new Date(res.ts).toLocaleString()}` : "ping-failed");

      // ===== DB 스모크 테스트 시작 =====
      const ok = await window.electron.db.settings.set("theme", "dark");
      console.log("db settings set ok:", ok);

      const theme = await window.electron.db.settings.get("theme");
      console.log("db settings get theme:", theme);

      await window.electron.db.eventLog.append("APP_BOOT", {
        theme,
        ts: Date.now(),
      });
      // ===== DB 스모크 테스트 끝 =====
    })();
  }, []);

  return <div className="p-4">pong: {pong}</div>;
}
